import React, { useState, useEffect } from 'react';
import styled, { createGlobalStyle } from 'styled-components';
import Quiz from './components/Quiz';
import Result from './components/Result';
import CreateQuiz from './components/CreateQuiz';
import ImprovementSuggestions from './components/ImprovementSuggestions';
import { loadSubmitters } from './utils/realtimeDatabase'; // 수정된 부분
import { categories as initialCategories } from './constants/categories';

const GlobalStyle = createGlobalStyle`
  body {
    font-family: Arial, Helvetica, sans-serif;
    background-color: #f0f2f5;
    margin: 0;
    padding: 0;
  }

  h1 {
    text-align: center;
    margin-top: 20px;
  }
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
`;

const Button = styled.button`
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 10px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 5px;

  &:hover {
    background-color: #0056b3;
  }
`;

const Select = styled.select`
  padding: 10px;
  font-size: 16px;
  margin: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
`;

const List = styled.ul`
  list-style: none;
  padding: 0;
`;

const ListItem = styled.li`
  padding: 10px;
  border-bottom: 1px solid #ccc;
`;

const App = () => {
  const [isQuizFinished, setIsQuizFinished] = useState(false);
  const [score, setScore] = useState(0);
  const [totalQuestions, setTotalQuestions] = useState(0);
  const [view, setView] = useState('home');
  const [selectedCategory, setSelectedCategory] = useState(initialCategories[0]);
  const [categories, setCategories] = useState(initialCategories);
  const [submitters, setSubmitters] = useState([]);

  useEffect(() => {
    const fetchSubmitters = async () => {
      const loadedSubmitters = await loadSubmitters();
      const sortedSubmitters = Object.entries(loadedSubmitters).sort((a, b) => b[1] - a[1]);
      setSubmitters(sortedSubmitters);
    };

    fetchSubmitters();
  }, []);

  const handleQuizEnd = (finalScore, totalQuestions) => {
    setScore(finalScore);
    setTotalQuestions(totalQuestions);
    setIsQuizFinished(true);
  };

  const handleViewChange = (newView) => {
    setView(newView);
    setIsQuizFinished(false);
  };

  const handleBack = () => {
    setView('home');
  };

  return (
    <>
      <GlobalStyle />
      <Container>
        <h1>Quiz App</h1>
        {view === 'home' && (
          <div>
            <Button onClick={() => handleViewChange('takeQuiz')}>퀴즈 풀기</Button>
            <Button onClick={() => handleViewChange('createQuiz')}>퀴즈 만들기</Button>
            <Button onClick={() => handleViewChange('suggestImprovements')}>원하는 개선사항</Button>
            <h2>가장 많이 제출한 제출자</h2>
            <List>
              {submitters.map(([submitter, count], index) => (
                <ListItem key={index}>{submitter}: {count} 문제</ListItem>
              ))}
            </List>
          </div>
        )}
        {view === 'takeQuiz' && (
          <div>
            <label>카테고리 선택:</label>
            <Select value={selectedCategory} onChange={(e) => setSelectedCategory(e.target.value)}>
              {categories.map((cat, index) => (
                <option key={index} value={cat}>{cat}</option>
              ))}
            </Select>
            {isQuizFinished ? (
              <Result score={score} totalQuestions={totalQuestions} />
            ) : (
              <Quiz category={selectedCategory} handleQuizEnd={handleQuizEnd} handleBack={handleBack} />
            )}
          </div>
        )}
        {view === 'createQuiz' && <CreateQuiz categories={categories} handleBack={handleBack} />}
        {view === 'suggestImprovements' && <ImprovementSuggestions handleBack={handleBack} />}
      </Container>
    </>
  );
};

export default App;
