import { ref, set, push, get, child } from 'firebase/database';
import { getStorage, ref as storageRef, uploadBytes, getDownloadURL } from 'firebase/storage';
import { db, storage } from '../firebase';

const QUESTIONS_REF = 'questions';
const IMPROVEMENTS_REF = 'improvements';

export const saveQuestion = async (question) => {
  const newQuestionRef = push(ref(db, `${QUESTIONS_REF}/${question.category}`));
  await set(newQuestionRef, question);
};

export const loadQuestions = async (category) => {
  const snapshot = await get(child(ref(db), `${QUESTIONS_REF}/${category}`));
  const questions = [];
  if (snapshot.exists()) {
    snapshot.forEach((childSnapshot) => {
      questions.push(childSnapshot.val());
    });
  }
  return questions;
};

export const saveImprovementSuggestion = async (suggestion) => {
  const newImprovementRef = push(ref(db, IMPROVEMENTS_REF));
  await set(newImprovementRef, { suggestion });
};

export const uploadImage = async (file) => {
  if (!file) return null;
  const imageRef = storageRef(storage, `images/${file.name}`);
  await uploadBytes(imageRef, file);
  return await getDownloadURL(imageRef);
};

// 여기 추가된 부분
export const loadSubmitters = async () => {
  const snapshot = await get(ref(db, QUESTIONS_REF));
  const submitters = {};
  if (snapshot.exists()) {
    snapshot.forEach((categorySnapshot) => {
      categorySnapshot.forEach((questionSnapshot) => {
        const question = questionSnapshot.val();
        if (question.submitter) {
          if (submitters[question.submitter]) {
            submitters[question.submitter]++;
          } else {
            submitters[question.submitter] = 1;
          }
        }
      });
    });
  }
  return submitters;
};
